.heading{
    font-family: "Comic sans MS";
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    border-radius: 2px;;
    box-shadow: 15px 10px 18px  brown,-5px -5px 18px green;
    width:fit-content;
  }
.thinking{
    width: 20%;
    margin-top: 5%;
    margin-left: auto;
    display: inline-block;
  }
  
  .questioning1{
    margin-right: 40%;
    text-align: center;
    display: inline-block;
  
  }
  .questioning{
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    display: inline-block;
  
  
  }
  .bottom-right{
    margin-left: auto;
    display: inline-block;
  }
  .bottom-left{
    margin-right: auto;
    margin-left: auto;
    display: inline-block;
  }
  .bottom{
    margin-right: auto;
    margin-left: auto;
    padding-right: 2%;
  
    display: inline-block;
  }
  .arrowbutton1{
    margin-left:27%;
    background-color: blue;
    width:40%;
    color:white;
  }
  .arrowbutton{

    background-color: blue;
    color:white;
  }

  .gotonxt1{
    margin-left:60%;
    margin-right: auto;
    margin-top: -22%;
    background:white;
    width: 350px;
    border-color: #888888;
    border-style: 2px solid;
    border-radius: 10px;
    box-shadow: 15px 10px 18px  #888888,-5px -5px 18px #888888;
    font-size: large;
    text-align: center;
    padding: 20px 20px;
  } 
  .gotonxt{
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    margin-bottom: auto; 
    background:white;
    width: 350px;
    box-shadow: 15px 10px 18px  #888888,-5px -5px 18px #888888;
    border-style: 2px solid;
    border-radius: 10px;
    
    font-size: large;
    text-align: center;
    padding: 20px 20px;
  }
 .secondclick{
  margin-top: auto;
  border: none;
  background-color: aliceblue;
  font-size: medium;
  border-bottom: 2px red;
  border-bottom: 6px solid blue;
 }
  
  .clickingonit{
    font-size: medium;
    color:red;
  }
  .secondclick:hover{
    background-color: bisque;
  }
  .arrowbutton:hover{
    background-color: aquamarine;
    
  }
  .aligncenter{
    text-align: center;
  }
  
@media  only screen  and (min-height:1000px)  {
    .App {
        text-align: center;
      }
      .App1 {
        text-align: center;
      }
      .heading{
        font-family: "Comic sans MS";
        margin-left: auto;
        margin-right: auto;
        text-align: center;
      }
      .thinking{
        width: 20%;
        margin-top: 5%;
        margin-left: auto;
        display: inline-block;
      }
      

      .questioning1{
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        display: inline-block;
      }
      .arrowbutton1{
    
        background-color: green;
        color:white;
      }
      .gotonxt1{
        margin-left: auto;
        margin-right: auto;
        margin-top:0%;
        margin-bottom: auto; 
        background:white;
        position:relative;
        width: 450px;
        border-color: #888888;
        border-style: 2px solid;
        border-radius: 10px;
        box-shadow: 15px 10px 18px  #888888,-5px -5px 18px #888888;
        font-size: x-large;
        text-align: center;
        padding: 20px 20px;
        font-weight: bold;
      }
      .clickingonit{
        font-size: x-large;
      }
      .secondclick{
        font-size: x-large;
      }
    
}


